<template>
    <div id="inputBox">
        <input type="text" v-model="userName" v-on:keyup.enter="getUserName" placeholder="소환사명을 입력해주세요">
        <i class="addBtn fa fa-plus" v-on:click="getUserName"></i>
    </div>
</template>
<script>
export default {
    name: 'UserNameInput',
    data() {
        return {
            userName: ''
        }
    },

    method:{
        getUserName() {
            if(this.userName) {
                userName = this.userName.trim();
                this.$emit('getUserName', userName);
                this.userName = '';
            }
        }
    }
    
    
}
</script>
<style>
    
</style>


